import React, { useState, useEffect } from 'react';
import articleContent from './article-content';
import ArticlesList from '../components/articles-list';
import NotFoundPage from '../pages/NotFoundPage';
import CommentsList from '../components/comments-list'
import UpvotesSection from '../components/upvote-section'
import AddCommentForm from '../components/add-comment-form'

const ArticlePage = ({ match }) => {


    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: []})
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    }, [name])


    if (!article) return <NotFoundPage />


    const otherArticles = articleContent.filter(otherArticle => otherArticle.name !== name)

    return (
        <>
        <h1>{article.title}</h1>
        <UpvotesSection articleName={article.name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
        {article.content.map((paragraph, key) => (
            <p key={key}>{paragraph}</p>
        ))}

        <CommentsList comments={articleInfo.comments} />
        <AddCommentForm articleName={article.name} setArticleInfo={setArticleInfo} />

        <h3>Other articles</h3>
        <ArticlesList articles={otherArticles} />
        </>
    );
}

export default ArticlePage;